import React, {useCallback, useEffect, useState} from 'react';
import PropTypes, {element} from 'prop-types';
import {Button, SegmentationGroupTable, useViewportGrid, ListMenu} from '@ohif/ui';
import callInputDialog from './callInputDialog';
import * as csTools from '@cornerstonejs/tools';
import BaseToolEdit from "./segToolsEditSection/baseToolEdit";
import SegmentsConfigurationImportExport from "./segConfigurationIO";
import SegmentationExport from "./segmentationExport";
import {useTranslation} from 'react-i18next';


const LABELMAP = csTools.Enums.SegmentationRepresentations.Labelmap;

export default function PanelSegmentation({
  servicesManager,
  commandsManager,
}) {
  const [{ activeViewportIndex, viewports }, viewportGridService] = useViewportGrid();
  const {
      segmentationService,
      uiDialogService,
      cornerstoneViewportService,
      toolGroupService,
      toolbarService,
      uiNotificationService
  } = servicesManager.services;

  const { t } = useTranslation('PanelSegmentation');
  const [selectedSegmentationId, setSelectedSegmentationId] = useState(null);
  const [segmentationConfiguration, setSegmentationConfiguration] = useState(
    segmentationService.getConfiguration()
  );

  const [segmentations, setSegmentations] = useState(() =>
    segmentationService.getSegmentations()
  );

  const [isMinimized, setIsMinimized] = useState({});

  const onToggleMinimizeSegmentation = useCallback(
    id => {
      setIsMinimized(prevState => ({
        ...prevState,
        [id]: !prevState[id],
      }));
    },
    [setIsMinimized]
  );

  // Only expand the last segmentation added to the list and collapse the rest
  useEffect(() => {
    const lastSegmentationId = segmentations[segmentations.length - 1]?.id;
    if (lastSegmentationId) {
      setIsMinimized(prevState => ({
        ...prevState,
        [lastSegmentationId]: false,
      }));
    }
  }, [segmentations, setIsMinimized]);

  useEffect(() => {
    // ~~ Subscription
    const added = segmentationService.EVENTS.SEGMENTATION_ADDED;
    const updated = segmentationService.EVENTS.SEGMENTATION_UPDATED;
    const removed = segmentationService.EVENTS.SEGMENTATION_REMOVED;
    const subscriptions = [];

    [added, updated, removed].forEach(evt => {
      const { unsubscribe } = segmentationService.subscribe(evt, () => {
        const segmentations = segmentationService.getSegmentations();
        setSegmentations(segmentations);
        setSegmentationConfiguration(segmentationService.getConfiguration());
      });
      subscriptions.push(unsubscribe);
    });

    return () => {
      subscriptions.forEach(unsub => {
        unsub();
      });
    };
  }, []);

  const onSegmentationClick = (segmentationId: string) => {
    segmentationService.setActiveSegmentationForToolGroup(segmentationId);
  };

  const onSegmentationDelete = (segmentationId: string) => {
    segmentationService.remove(segmentationId);
  };

  const getToolGroupIds = segmentationId => {
    const toolGroupIds = segmentationService.getToolGroupIdsWithSegmentation(
      segmentationId
    );

    return toolGroupIds;
  };

  const onSegmentClick = (segmentationId, segmentIndex) => {
    segmentationService.setActiveSegmentForSegmentation(
      segmentationId,
      segmentIndex
    );

    const toolGroupIds = getToolGroupIds(segmentationId);

    toolGroupIds.forEach(toolGroupId => {
      // const toolGroupId =
      segmentationService.setActiveSegmentationForToolGroup(
        segmentationId,
        toolGroupId
      );
      segmentationService.jumpToSegmentCenter(
        segmentationId,
        segmentIndex,
        toolGroupId
      );
    });
  };

  const onSegmentEdit = (segmentationId, segmentIndex) => {
    const segmentation = segmentationService.getSegmentation(segmentationId);

    const segment = segmentation.segments[segmentIndex];
    const { label } = segment;

    callInputDialog(uiDialogService, label, (label, actionId) => {
      if (label === '') {
        return;
      }

      segmentationService.setSegmentLabelForSegmentation(
        segmentationId,
        segmentIndex,
        label
      );
    });
  };

  const onSegmentAdd = (segmentationId) => {
    const currentSegmentation = segmentationService.getSegmentation(segmentationId)
    segmentationService.addSegment(
        segmentationId,
        currentSegmentation.segments.length,
        viewports[activeViewportIndex].viewportOptions.toolGroupId,
        {
          label: `Segmentation ${currentSegmentation.segmentCount + 1}`,
          color: [Math.floor(Math.random() * 256), Math.floor(Math.random() * 256), Math.floor(Math.random() * 256)],
          opacity: 255,
          visibility: true,
          isLocked: false,
          active: true
        }
    )
  }

  const onSegmentationEdit = segmentationId => {
    const segmentation = segmentationService.getSegmentation(segmentationId);
    const { label } = segmentation;

    callInputDialog(uiDialogService, label, (label, actionId) => {
      if (label === '') {
        return;
      }

      segmentationService.addOrUpdateSegmentation(
        {
          id: segmentationId,
          label,
        },
        false, // suppress event
        true // notYetUpdatedAtSource
      );
    });
  };

  const onSegmentColorChange = (segmentationId, segmentIndex, newColor) => {
    segmentationService.setSegmentColor(
        segmentationId,
        segmentIndex,
        newColor,
        viewports[activeViewportIndex].viewportOptions.toolGroupId
    )
    return;
  };

  const onSegmentDelete = (segmentationId, segmentIndex) => {
    segmentationService.removeSegment(
      segmentationId,
      segmentIndex
    );
  };

  const onToggleSegmentVisibility = (segmentationId, segmentIndex) => {
    const segmentation = segmentationService.getSegmentation(segmentationId);
    const segmentInfo = segmentation.segments[segmentIndex];
    const isVisible = !segmentInfo.isVisible;
    const toolGroupIds = getToolGroupIds(segmentationId);

    // Todo: right now we apply the visibility to all tool groups
    toolGroupIds.forEach(toolGroupId => {
      segmentationService.setSegmentVisibility(
        segmentationId,
        segmentIndex,
        isVisible,
        toolGroupId
      );
    });
  };

  const onToggleSegmentationVisibility = segmentationId => {
    segmentationService.toggleSegmentationVisibility(segmentationId);
  };

  const _setSegmentationConfiguration = useCallback(
    (segmentationId, key, value) => {
      segmentationService.setConfiguration({
        segmentationId,
        [key]: value,
      });
    },
    [segmentationService]
  );

  const createSegmentationForSelectedViewport = () => {
    viewports.forEach(vp => {
      //Select chosen active viewport to create a segmentation on
      if(vp.viewportIndex === activeViewportIndex){
        if(vp.viewportOptions.viewportType === 'volume3d'){
          uiNotificationService.show({
            title: 'Create Segmentation',
            message: 'Can\'t create segmentation in viewport 3D, select a 2D viewport',
            type: 'error',
          });
          return
        }

        console.log('SEG VP', vp)
        segmentationService.createSegmentationForDisplaySet(
            vp.displaySetInstanceUIDs[0]
        ).then(segId => {
          const segmentation = segmentationService.getSegmentation(segId)
          console.log('CREATED SEGMENTATION', segmentation)
          segmentationService.addSegmentationRepresentationToToolGroup(
              vp.viewportOptions.toolGroupId,
              segId,
              true,
              LABELMAP
          ).then(() => {
            segmentationService.setActiveSegmentationForToolGroup(
                segId,
                vp.viewportOptions.toolGroupId
            );
            segmentationService.addSegment(
                segId,
                1,
                viewports[activeViewportIndex].viewportOptions.toolGroupId,
                {
                  label: `Segmentation 1`,
                  color: [255, 0, 0],
                  opacity: 255,
                  visibility: true,
                  isLocked: false,
                  active: true
                }
            )
            //Add segmentation tools to toolbar
            console.log('MTOOLS', toolbarService.getButtonSection('MeasurementTools', {}))
            toolbarService.createButtonSection('primary', [
                'MeasurementTools',
                'Zoom',
                'WindowLevel',
                'Pan',
                'Capture',
                'Crosshairs',
                'MoreTools',
                'SegmentationTools'
            ])
          });

        })
      }
    })
  }

  // useEffect(() => {
  //   console.log('SEGMENTATIONS', segmentations)
  // },)

  const isSegmentationForDisplaySet = () => {
    const displaySetUID = viewports.filter(vp => vp.viewportIndex === activeViewportIndex)[0]?.displaySetInstanceUIDs[0]
    const matchingSegs = segmentations.filter(seg => seg.displaySetInstanceUID === displaySetUID)
    return matchingSegs.length > 0;
  }

  return (
    <div className="flex flex-col flex-auto min-h-0F mt-1">
      {/* Segmentation tool edition */}
      <BaseToolEdit
          segmentationService={segmentationService}
          cornerstoneViewportService={cornerstoneViewportService}
          toolGroupService={toolGroupService}
          toolbarService={toolbarService}
          commandsManager={commandsManager}
      />
      {isSegmentationForDisplaySet() && (
          <SegmentsConfigurationImportExport
              segmentationService={segmentationService}
              segmentations={segmentations}
          />
      )}
      {/* show segmentation table */}
      {isSegmentationForDisplaySet() ? (
        <SegmentationGroupTable
          title={t('Segmentations')}
          showAddSegment={true}
          segmentations={segmentations}
          isMinimized={isMinimized}
          activeSegmentationId={selectedSegmentationId || ''}
          onSegmentationClick={onSegmentationClick}
          onSegmentationDelete={onSegmentationDelete}
          onSegmentationEdit={onSegmentationEdit}
          onSegmentClick={onSegmentClick}
          onSegmentEdit={onSegmentEdit}
          onSegmentAdd={onSegmentAdd}
          onSegmentColorChange={onSegmentColorChange}
          onSegmentDelete={onSegmentDelete}
          onToggleSegmentVisibility={onToggleSegmentVisibility}
          onToggleSegmentationVisibility={onToggleSegmentationVisibility}
          onToggleMinimizeSegmentation={onToggleMinimizeSegmentation}
          segmentationConfig={{ initialConfig: segmentationConfiguration }}
          setRenderOutline={value =>
            _setSegmentationConfiguration(
              selectedSegmentationId,
              'renderOutline',
              value
            )
          }
          setOutlineOpacityActive={value =>
            _setSegmentationConfiguration(
              selectedSegmentationId,
              'outlineOpacity',
              value
            )
          }
          setRenderFill={value =>
            _setSegmentationConfiguration(
              selectedSegmentationId,
              'renderFill',
              value
            )
          }
          setRenderInactiveSegmentations={value =>
            _setSegmentationConfiguration(
              selectedSegmentationId,
              'renderInactiveSegmentations',
              value
            )
          }
          setOutlineWidthActive={value =>
            _setSegmentationConfiguration(
              selectedSegmentationId,
              'outlineWidthActive',
              value
            )
          }
          setFillAlpha={value =>
            _setSegmentationConfiguration(
              selectedSegmentationId,
              'fillAlpha',
              value
            )
          }
          setFillAlphaInactive={value =>
            _setSegmentationConfiguration(
              selectedSegmentationId,
              'fillAlphaInactive',
              value
            )
          }
        />
      ) : null}
      {isSegmentationForDisplaySet() ? (
              <SegmentationExport segmentationService={segmentationService} segmentations={segmentations}/>
          ) : segmentations.length === 0 && (
          <Button
              className={'pt-1/10 pb-1/10'}
              onClick={createSegmentationForSelectedViewport}
          >
            {"Create Segmentation For Selected Viewport"}
          </Button>
          )
      }
    </div>
  );
}

PanelSegmentation.propTypes = {
  commandsManager: PropTypes.shape({
    runCommand: PropTypes.func.isRequired,
  }),
  servicesManager: PropTypes.shape({
    services: PropTypes.shape({
      segmentationService: PropTypes.shape({
        getSegmentation: PropTypes.func.isRequired,
        getSegmentations: PropTypes.func.isRequired,
        toggleSegmentationVisibility: PropTypes.func.isRequired,
        subscribe: PropTypes.func.isRequired,
        EVENTS: PropTypes.object.isRequired,
      }).isRequired,
    }).isRequired,
  }).isRequired,
};
